import { Node } from 'cc';
import { GameArea } from './GameArea';

export class GameController {
    constructor() {
        if (GameController.instance != null) {
            console.log("singlon instance error")
        }

    }


    private static instance: GameController = null;

    public static get Instance() {
        if (this.instance == null) {
            this.instance = new GameController();
        }
        return GameController.instance;
    }

    gameArea: Node;

    public test(pos, block) {
        let targetPos = this.gameArea.getComponent(GameArea).getGridByPosition(pos);
        this.add(targetPos, block)
    }

    public add(pos, block) {
        this.gameArea.getComponent(GameArea).addBlock(pos, block);


    }

}